import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';

import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const authorspanel = ({ id, this: love}) => (
    <Panel id={id}>
        <header> </header>
        <div className="titlesbiography"  onClick={() => love.goBack()}>
            <Div>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Книги автора
            </Div>
        </div>
        {love.state.authorbooks.map(function(result, i) {
            return (
            <div key={i}>
                <div className="allbooks">
                    <img onClick={() => love.goBookbyBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBookbyBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                    <div className="contentviews">
                        Просмотров: {result['views']}
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) }
    </Panel>
);
authorspanel.propTypes = {
    id: PropTypes.string.isRequired
};
export default authorspanel;